<template>
	<div>
		<el-tree :data="treeData" ref="tree" :props="props" :load="loadNode" lazy node-key="id">
			<span class="custom-tree-node" slot-scope="{ node, data }">
				<span>{{ node.label }}</span>
				<span>
					<el-button type="text" size="mini" @click.stop="openEditForm(node,data)">
						
					</el-button>
					<el-button type="text" size="mini" @click.stop="openAppendForm(node,data)">
						
					</el-button>
					<el-button type="text" size="mini" @click.stop="openDeleteConfirm(node, data)">
						
					</el-button>
				</span>
			</span>
		</el-tree>
		<!---->
		<el-dialog title="" :visible.sync="editFormVisible">
			<el-form :model="editForm" label-position="left" label-width="80px">
				<el-form-item label="">
					<el-input v-model="editForm.name"></el-input>
				</el-form-item>
				<el-form-item label="">
					<!---->
					<div class="upload-box">
						<div v-show="editForm.src" class="preview">
							<img :src="editForm.src" alt="" />
							<i @click="deletePicHandle(editForm.src,'editForm')" class="el-icon-delete"></i>
						</div>
						<div v-show="!editForm.src" class="upload-button">
							<i class="el-icon-plus"></i>
							<input type="file" @change="uploadHandle($event,'editForm')" />
						</div>
					</div>
				</el-form-item>
			</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button @click="editFormVisible = false"> </el-button>
				<el-button type="primary" @click="editNode"> </el-button>
			</div>
		</el-dialog>
		<!---->
		<el-dialog title="" :visible.sync="appendFormVisible">
			<el-form :model="appendForm" label-position="left" label-width="80px">
				<el-form-item label="">
					<el-input v-model="appendForm.name"></el-input>
				</el-form-item>
				<el-form-item label="">
					<!---->
					<div class="upload-box">
						<div v-show="appendForm.src" class="preview">
							<img :src="appendForm.src" alt="" />
							<i @click="deletePicHandle(appendForm.src,'appendForm')" class="el-icon-delete"></i>
						</div>
						<div v-show="!appendForm.src" class="upload-button">
							<i class="el-icon-plus"></i>
							<input type="file" @change="uploadHandle($event,'appendForm')" />
						</div>
					</div>
				</el-form-item>
			</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button @click="closeAppendForm"> </el-button>
				<el-button type="primary" @click="appendNode"> </el-button>
			</div>
		</el-dialog>
	</div>
</template>
<script>
	export default {
		data() {
			return {
				props: {
					label: 'name',
					children: 'zones',
				},
				treeData: [],
				//
				editFormVisible: false,
				editForm: {
					name: "",
					src: ""
				},
				//
				appendFormVisible: false,
				appendForm: {
					name: "",
					src: ""
				},
				//
				nodeData: {}
			};
		},
		methods: {
			/**/
			loadNode(node, resolve) {
				let id = node.data.id;
				//
				if(node.level == 0) {
					id = 0;
				}
				this.axios.get('/api/category/sub/', {
						params: {
							pId: id
						}
					})
					.then((result) => {
						if(result.data.status) {
							resolve(result.data.data);
						}
					});
			},
			//
			uploadHandle(event, target) {
				//file
				let file = event.target.files[0];
				let formData = new FormData();
				formData.append("file", file);
				//ajax
				this.axios
					.post('/api/upload/common/', formData)
					.then((result) => {
						if(result.data.errno == 0) {
							//fileInputvalue.
							event.target.value = "";
							this[target].src = result.data.data[0];
						}
					});
			},
			//
			deletePicHandle(src, target) {
				this.axios
					.post('/api/upload/delete/', {
						src: '.' + src
					})
					.then((result) => {
						if(result.data.status) {
							this[target].src = '';
						}
					});
			},
			//
			openAppendForm(node, data) {
				this.appendFormVisible = true;
				this.nodeData = data;
			},
			//
			closeAppendForm() {
				this.appendFormVisible = false;
				this.appendForm.name = '';
				this.appendForm.src = '';
			},
			//
			appendNode() {
				let newChild = {
					name: this.appendForm.name,
					pId: this.nodeData.id,
					level: this.nodeData.level+1,
					img: this.appendForm.src
				}
				this.axios
					.post('/api/category/add/', newChild)
					.then((result) => {
						if(result.data.status) {
							newChild.id = result.data.data.id;
							this.$refs.tree.append(newChild, this.nodeData);
							//
							this.closeAppendForm();
						} else {
							alert(result.data.msg);
						}
					});
			},
			openEditForm(node, data) {
				this.editForm.name = data.name;
				this.editForm.src = data.img;
				this.nodeData = data;
				this.editFormVisible = true;
			},
			//
			editNode() {
				this.axios.post('/api/category/update/', {
						id: this.nodeData.id,
						name: this.editForm.name,
						img: this.editForm.src
					})
					.then((result) => {
						if(result.data.status) {
							//
							this.nodeData.name = this.editForm.name;
							//
							this.editFormVisible = false;
						}
					})
			},
			//
			openDeleteConfirm(node, data) {
				this
					.$confirm(`[${data.name}], ?`, '', {
						type: 'warning'
					})
					.then(() => {
						//
						return this.axios.post('/api/category/delete/', {
							id: data.id
						});
					}).then((result) => {
						if(result.data.status) {
							this.$refs.tree.remove(data);
							this.$message({
								type: 'success',
								message: '!'
							});
						} else {
							alert(result.data.msg);
						}
					})
					.catch(() => {
						this.$message({
							type: 'info',
							message: ''
						});
					});
			},
		}
	};
</script>
<style lang="scss" scoped>
	.custom-tree-node {
		flex: 1;
		display: flex;
		align-items: center;
		justify-content: space-between;
		font-size: 14px;
		padding-right: 8px;
	}
	
	.upload-button {
		position: relative;
		background-color: #fbfdff;
		border: 1px dashed #c0ccda;
		border-radius: 6px;
		width: 148px;
		height: 148px;
		line-height: 146px;
		font-size: 30px;
		text-align: center;
		input {
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
			opacity: 0;
		}
	}
	
	.upload-box {
		display: flex;
		.preview {
			width: 148px;
			height: 148px;
			overflow: hidden;
			position: relative;
			font-size: 14px;
			margin-right: 10px;
			border: 1px dashed #c0ccda;
			border-radius: 6px;
			img {
				max-width: 100%;
			}
			i {
				position: absolute;
				top: 3px;
				right: 3px;
			}
		}
	}
</style>