<template>
	<div>
		<div class="clearfix am-margin-bottom-lg">
			<div class="pull-left">
				<h3 class="title"></h3>
			</div>
			<div class="pull-right">
				<el-breadcrumb separator-class="el-icon-arrow-right">
					<el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
					<el-breadcrumb-item></el-breadcrumb-item>
					<el-breadcrumb-item></el-breadcrumb-item>
					<el-breadcrumb-item></el-breadcrumb-item>
				</el-breadcrumb>
			</div>
		</div>
		<el-form :inline="true" class="">
			<el-form-item>
				<el-input placeholder=""></el-input>
			</el-form-item>
			<el-form-item>
				<el-button type="primary"></el-button>
			</el-form-item>
			<el-form-item>
				<el-button type="primary" @click="handleNew"></el-button>
			</el-form-item>
		</el-form>
		<el-table ref="multipleTable" tooltip-effect="dark" style="width: 100%" :data="tableData">
			<el-table-column type="selection" width="55">
			</el-table-column>
			<el-table-column type="index">
			</el-table-column>
			<el-table-column prop="name" sortable label="" width="120">
			</el-table-column>
			<el-table-column prop="sex" sortable label="" width="120">
			</el-table-column>
			<el-table-column prop="age" sortable label="" width="120">
			</el-table-column>
			<el-table-column prop="birthday" sortable label="" width="140">
			</el-table-column>
			<el-table-column prop="address" sortable label="">
			</el-table-column>
			<el-table-column label="" width="200">
				<template slot-scope="scope">
					<el-button size="mini" @click="handleEdit(scope.$index, scope.row)"></el-button>
					<el-button size="mini" type="danger" @click="handleDelete(scope.$index, scope.row)"></el-button>
				</template>
			</el-table-column>
		</el-table>
		<el-pagination background layout="prev, pager, next" :total="120">
		</el-pagination>
		<!---->
		<el-dialog title="" :visible.sync="adding">
			<el-form :model="form" :rules="rules" ref="ruleForm" :label-position="'left'" label-width="80px">
				<el-form-item label="" prop="name">
					<el-input v-model="form.name" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="" prop="sex">
					<el-radio-group v-model="form.sex">
						<el-radio label=""></el-radio>
						<el-radio label=""></el-radio>
					</el-radio-group>
				</el-form-item>
				<el-form-item label="" prop="age">
					<el-radio-group v-model="form.age">
						<el-input type="age" v-model.number="form.age" auto-complete="off"></el-input>
					</el-radio-group>
				</el-form-item>
				<el-form-item label="" prop="birthday">
					<el-radio-group v-model="form.birthday">
						<el-date-picker type="date" placeholder="" v-model="form.birthday" value-format="yyyy-MM-dd" style="width: 100%;"></el-date-picker>
					</el-radio-group>
				</el-form-item>
				<el-form-item label="" prop="address">
					<el-input type="textarea" v-model="form.address"></el-input>
				</el-form-item>
			</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button @click="adding = false"> </el-button>
				<el-button type="primary" @click="submitForm('ruleForm')"> </el-button>

			</div>
		</el-dialog>
		<!---->
		<el-dialog title="" :visible.sync="editing">
			<el-form :model="form" :rules="rules" ref="ruleForm" :label-position="'left'" label-width="80px">
				<el-form-item label="" prop="name">
					<el-input v-model="form.name" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="" prop="sex">
					<el-radio-group v-model="form.sex">
						<el-radio label=""></el-radio>
						<el-radio label=""></el-radio>
					</el-radio-group>
				</el-form-item>
				<el-form-item label="" prop="age">
					<el-radio-group v-model="form.age">
						<el-input type="age" v-model.number="form.age" auto-complete="off"></el-input>
					</el-radio-group>
				</el-form-item>
				<el-form-item label="" prop="birthday">
					<el-radio-group v-model="form.birthday">
						<el-date-picker type="date" placeholder="" v-model="form.birthday" value-format="yyyy-MM-dd" style="width: 100%;"></el-date-picker>
					</el-radio-group>
				</el-form-item>
				<el-form-item label="" prop="address">
					<el-input type="textarea" v-model="form.address"></el-input>
				</el-form-item>
			</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button @click="editing = false"> </el-button>
				<el-button type="primary" @click="updateForm('ruleForm')"> </el-button>
			</div>
		</el-dialog>
		<!---->
		<el-dialog title="" :visible.sync="confirmVisible" width="30%">
			<span>?</span>
			<span slot="footer" class="dialog-footer">
				<el-button @click="confirmVisible = false"> </el-button>
				<el-button type="primary" @click="confirmDelete"> </el-button>
			</span>
		</el-dialog>
	</div>

</template>

<script>
//service
import { addStudent } from "@/assets/js/service";

export default {
  name: "list",
  data() {
    const item = {
      name: "",
      sex: "",
      age: 30,
      birthday: "2016-05-02",
      address: " 1518 "
    };
    return {
      tableData: Array(10).fill(item),
      adding: false,
      editing: false,
      confirmVisible: false,
      form: {
        name: "",
        sex: "",
        age: 0,
        birthday: "",
        address: ""
      },
      rules: {
        name: [
          {
            required: true,
            message: "",
            trigger: "blur"
          },
          {
            min: 3,
            max: 5,
            message: " 3  5 ",
            trigger: "blur"
          }
        ],
        sex: [
          {
            required: true,
            message: "",
            trigger: "change"
          }
        ],
        age: [
          {
            required: true,
            message: ""
          },
          {
            type: "integer",
            min: 0,
            max: 150,
            message: "0~150"
          }
        ],
        birthday: [
          {
            required: true,
            message: "",
            trigger: "change"
          }
        ],
        address: [
          {
            required: true,
            message: "",
            trigger: "blur"
          }
        ]
      }
    };
  },
  methods: {
    handleNew() {
      this.adding = true;
      this.form = {};
    },
    handleEdit(index, row) {
      this.editing = true;
      this.form = row;
    },
    handleDelete(index, row) {
      this.confirmVisible = true;
      console.log(index, row);
    },
    //index
    confirmDelete() {},
    updateForm(formName) {},
    submitForm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          //ajax
          addStudent(this.form).then(result => {
            if (result.data.status) {
              //dialog
              this.adding = false;
              //
              this.tableData.push(this.form);
            } else {
              alert(result.data.msg);
            }
          });
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    }
  }
};
</script>

<style scoped>
.title {
  margin: 0;
}
</style>
